<template>
  <div class="dispatch">
    <h2 @click="show_c()">調度控制台</h2>
    <MBox :title="'訂單詳細資料'">
      <div class="dispatch-mbox">
		<div>
		  <h3>訂單資料</h3>
          <div class="more-d"><span>訂單編號: {{c_order.SOrderNo}}</span><hr></div>
		  <div class="more-d"><span>行程: {{c_order.IsBack ? "是":"否"}}</span><hr></div>
		  <div class="more-d"><span>共乘: {{c_order.CanShared ? "是":"否"}}</span><hr></div>
		  <div class="more-d"><span>輪椅類型: {{c_order.WheelchairId}}</span><hr></div>
		  <div class="more-d"><span>陪同人數: {{c_order.FamilyWith}}</span><hr></div>
		  <div class="more-d"><span>起點: {{c_order.FromAddr}}</span><hr></div>
		  <div class="more-d"><span>迄點: {{c_order.ToAddr}}</span><hr></div>
	    </div>
		<div>
		  <h3>個案資料</h3>
		  <div class="more-d"><span>個案編號: {{c_order.CaseUserNo}}</span><hr></div>
          <div class="more-d"><span>個案姓名: {{c_order.CaseUserName}}</span><hr></div>
		  <div class="more-d"><span>市話: {{c_order.CaseUserTel}}</span><hr></div>
		  <div class="more-d"><span>手機: {{c_order.CaseUserPhone}}</span><hr></div>
		  <div class="more-d"><span>身分證字號: {{c_order.CaseUserUID}}</span><hr></div>
	    </div>
	  </div>
	</MBox>
	<div class="dispatch-order">
	  <div class="dispatch-order-d" v-for="(item,index) in order" draggable=true @dragstart="dragStart($event,item)" @dblclick="c_order=item;MBox.switch_open()">
	    {{item.SOrderNo}}
	  </div>
	</div>
	<div class="dispatch-d">
    <table border="1" class="dispatch-table">
      <tr>
        <th>車輛\時間</th>
        <th v-for="(item,index) in time">{{item}}</th>
      </tr>
      <tr v-for="(item,index) in car" @drop="post_des($event,item)" @dragover="dragover($event)">
        <th>{{item.CarNo}}</th>
        <td v-for="(item2,index2) in time">
          <div class="dispatch-table-d" v-for="(item3,index3) in dispatch" v-if="item3.DespatchDetails[0].CarNo==item.CarNo&&item3.time==item2">
			<div class="dispatch-table-d-d" v-for="(item4,index4) in item3.DespatchDetails" @contextmenu="menu_open($event,item4)">
			  <div class="dispatch-table-d-d-d" style="font-weight: bold;">{{item4.CaseUser.Name}} ({{item4.OrderDetails.ExpectedMinute}}分鐘)</div>
			  <div class="dispatch-table-d-d-d">{{item4.OrderDetails.ToAddr}}</div>
			  <i class="el-icon-close dispatch-table-d-d-x"></i>
			  <div class="dispatch-table-d-d-menu" v-if="item4.open">
			    <div class="dispatch-table-d-d-menu-d">推播此訂單</div>
				<div class="dispatch-table-d-d-menu-d">變更司機</div>
				<div class="dispatch-table-d-d-menu-d">刪除訂單</div>
			  </div>
			</div>
		  </div>
        </td>
      </tr>
     </table>
	  <div class="dispatch-d-row" v-if="show_row">
		<div class="dispatch-d-row-d">車輛\時間</div>
	    <div class="dispatch-d-row-d" v-for="(item,index) in time">{{item}}</div>
	  </div>
	  <div class="dispatch-d-cell" v-if="show_cell">
	    <div class="dispatch-d-cell-d" v-for="(item,index) in car">{{item.CarNo}}</div>
	  </div>
	</div>
  </div>
</template>

<script>
import moment from "moment";
import MBox from './MBox'
export default {
  data() {
    return {
      time: [
		"05:00",
		"05:15",
		"05:30",
		"05:45",
        "06:00",
        "06:15",
        "06:30",
        "06:45",
        "07:00",
        "07:15",
        "07:30",
        "07:45",
        "08:00",
        "08:15",
        "08:30",
        "08:45",
        "09:00",
        "09:15",
        "09:30",
        "09:45",
        "10:00",
        "10:15",
        "10:30",
        "10:45",
        "11:00",
        "11:15",
        "11:30",
        "11:45",
        "12:00",
        "12:15",
        "12:30",
        "12:45",
        "13:00",
        "13:15",
        "13:30",
        "13:45",
        "14:00",
        "14:15",
        "14:30",
        "14:45",
        "15:00",
        "15:15",
        "15:30",
        "15:45",
        "16:00",
        "16:15",
        "16:30",
        "16:45",
        "17:00",
        "17:15",
        "17:30",
        "17:45",
        "18:00",
        "18:15",
        "18:30",
        "18:45",
        "19:00",
        "19:15",
        "19:30",
        "19:45",
        "20:00",
        "20:15",
        "20:30",
        "20:45",
        "21:00",
        "21:30",
        "21:45",
        "22:00",
        "22:15",
        "22:30",
        "22:45",
        "23:00"
      ],
	  order: "",
	  c_order:"",
      dispatch: "",
      car: "",
	  day: moment().format("YYYY-MM-DD"),
	  show_row:false,
	  show_cell:false
    };
  },
  components: {MBox},
  mounted: async function() {
    this.GLOBAL.show_black = true;
    await this.all_ready();
    await this.get_data();
    this.GLOBAL.show_black = false;
    console.log(moment().format());
  },
  watch: {
    async $route(to, from) {
      this.GLOBAL.show_black = true;
      await this.get_data();
      this.GLOBAL.show_black = false;
    }
  },
  methods: {
    get_data: async function(x) {
      this.order = await this.Function.get_dispatch_order(this.day);
      this.dispatch = await this.Function.get_dispatch_allgroup(this.day);
      this.car = await this.Function.get_dispatch_car();
      for (let i in this.dispatch) {
        this.dispatch[i].time = moment(this.dispatch[i].DespatchDetails[0].OrderDetails.ReservationDate).format("HH:mm");
	    for(let j in this.dispatch.DespatchDetails) {
          this.dispatch[i].DespatchDetails[j].open=false
		} 
	  }
	  console.log(this.dispatch);
	  this.re_scroll()
	},
	show_c:function() {
      console.log(this.dispatch);
	},
	re_scroll:async function() {
	  var that=this
      var myScroll = new IScroll(document.querySelectorAll(".dispatch-d")[0], {
        scrollX: true,
		scrollY: true,
		probeType: 3,
        mouseWheel: false,
        disablePointer: true,
        disableTouch: false,
        disableMouse: false
	  })
	  myScroll.on('scroll', function () {
        if(this.x<=-205) {
		  that.show_cell=true
		  that.re_cell()
		}
		else {
		  that.show_cell=false
		}
	  });
	},
	re_cell:function() {
	  var cell=document.querySelectorAll(".dispatch-d-cell-d")
	  var tr=document.querySelectorAll(".dispatch-table tr")
	  for(let i=0;i<cell.length;i++) {
		cell[i].style.height=tr[i+1].offsetHeight+"px"
		cell[i].style.lineHeight=tr[i+1].offsetHeight+"px"
		console.log(tr[i+1].offsetHeight)
	  }
	},
	menu_open:function(e,x) {
	  e.preventDefault();
	  if(x.open) {
		x.open=false
		this.$forceUpdate()
		return 0
	  }
      for (let i in this.dispatch) {
	    for(let j in this.dispatch[i].DespatchDetails) {
          this.dispatch[i].DespatchDetails[j].open=false
		} 
	  }
	  x.open=!x.open
	  this.$forceUpdate()
	},
	dragStart:function(e,x) {
      e.dataTransfer.setData("Text", JSON.stringify(x));
	},
	dragover:function(e) {
      e.preventDefault();
	},
	post_des:async function(e,x) {
	  e.preventDefault();
	  var data = JSON.parse(e.dataTransfer.getData("Text"))
	  var obj = {
		"DriverInfoId": x.DriverId,
		"CarId": x.Id,
	    "StartDate": moment(data.ReservationDate).format("YYYY-MM-DD HH:mm"),
		"DespatchDetail": [
		  { "OrderDetailId": data.Id }
		]
	  }	
      alert(await this.Function.post_Despatch(obj))
	}
  }
};
</script>


