<template>
  <div class="mbox" v-if="open">
    <div class="mbox-black" @click="switch_open"></div>
    <div class="mbox-d">
      <div class="mbox-d-h">匯出</div>
      <div class="mbox-d-c">
        <div>
          <el-date-picker v-model="time1" value-format="yyyy-MM-dd" type="date" placeholder="請輸入開始日期"></el-date-picker>
          <el-date-picker v-model="time2" value-format="yyyy-MM-dd" type="date" placeholder="請輸入結束日期"></el-date-picker>
          <div class="bt" @click="sub()">確定提交</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
export default {
  data() {
    return {
      open:false,
      time1:"",
      time2:""
	}
  },
  mounted :async function() {
    Vue.prototype.ExportTable=this
    this.re_size()
  },
  methods:{
    re_size:async function() {
      var dom=document.querySelectorAll(".mbox-d")[0]
      if (!dom) {
        return 0
      }
      var w=dom.offsetWidth
      var h=dom.offsetHeight
      dom.style.margin=-(h/2)+"px 0 0 "+-(w/2)+"px"
      console.log(dom)
    },
    switch_open:async function() {
      this.open=!this.open
      await this.Function.delay(0)
      this.re_size()
    }
  }
}
</script>


